import { createSlice } from "@reduxjs/toolkit";
import { userLogin, userLoguut } from "./userActions";

export const userSlice = createSlice({
  name: "userSlice",
  initialState: {
    user: [],
    errors: [],
    isLoggedIn: false
  },
  reducers: {},
  extraReducers: {
    // Login
    [userLogin.pending]: (state, { payload }) => {
      state.isLoggedIn = false;
      state.user = [];
      state.errors = [];
    },
    [userLogin.fulfilled]: (state, { payload }) => {
      switch (payload?.status) {
        case 200:
          state.user = payload?.data?.data;
          state.isLoggedIn = true;
          localStorage.setItem("token", payload?.data?.data?.token);
          break;
        case 422:
          state.isLoggedIn = false;
          state.errors = payload?.data;
          break;
        default:
          state.isLoggedIn = false;
      }
    },
    [userLogin.rejected]: (state, { payload }) => {
      console.log("rejected...");
    },
    // Logout
    [userLoguut.pending]: (state, { payload }) => {
      state.isLoggedIn = false;
      state.user = [];
      state.errors = [];
    },
    [userLoguut.fulfilled]: (state, { payload }) => {
      // const status = payload?.status;
      // if (status === 200 || status === 401 || status === 403) {
      localStorage.setItem("token", "");
      state.user = [];
      state.errors = [];
      state.isLoggedIn = false;
      // }
    },
    [userLoguut.rejected]: (state, { payload }) => {
      console.log("rejected...");
    }
  }
});

export const {} = userSlice.actions;
export default userSlice.reducer;
