import React, { memo, useEffect, useState } from "react";
import "../ManageBlogs.css";
import LineTableBlogs from "./LineTableBlogs";
import { useDispatch, useSelector } from "react-redux";
import { getBlogs } from "../../../store/blogs/blogsActions";
import Pagination from "./Pagination";

const TableBlogs = ({ filter, setFilter }) => {
  const dispatch = useDispatch();
  const allData = useSelector((state) => state.blogsSlice);

  useEffect(() => {
    dispatch(getBlogs(filter));
  }, [dispatch, filter]);
  // console.log(TableBlogs);
  return (
    <div>
      {allData?.isLoading ? (
        <h1>Is loading ...</h1>
      ) : (
        <div>
          {allData?.errors?.message ? (
            <h1>{allData?.errors?.message}</h1>
          ) : (
            <div>
              <table className="table table-striped table-hover">
                <thead>
                  <tr>
                    <th className="header-id" scope="col">
                      ID
                    </th>
                    <th className="header-title" scope="col">
                      Title
                    </th>
                    <th className="header-body" scope="col">
                      Body
                    </th>
                    <th className="header-actions" scope="col">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {allData?.blogs?.map((blog) => (
                    <LineTableBlogs key={blog.id} blog={blog} />
                  ))}
                </tbody>
              </table>
              <Pagination
                countPerPage={allData?.countPerPage}
                filter={filter}
                setFilter={setFilter}
              />
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default memo(TableBlogs);
