import { createSlice } from "@reduxjs/toolkit";
import { getBlogs, createBlog } from "./blogsActions";

export const blogsSlice = createSlice({
  name: "blogsSlice",
  initialState: {
    blogs: [],
    errors: [],
    isLoading: false,
    isSuccess: false,
    isDone: false,
    countPerPage: 0
  },
  reducers: {},
  extraReducers: {
    // Get blogs
    [getBlogs.pending]: (state, { payload }) => {
      state.isLoading = true;
      state.isSuccess = false;
      state.blogs = [];
      state.errors = [];
    },
    [getBlogs.fulfilled]: (state, { payload }) => {
      state.isLoading = false;
      if (payload?.status === 200) {
        state.isSuccess = true;
        state.blogs = payload?.data?.data;
        state.countPerPage = payload?.data?.meta?.last_page;
      } else {
        state.errors = { message: "Error no data available !" };
      }
    },
    [getBlogs.rejected]: (state, { payload }) => {
      console.log("getBlogs is rejected ...");
    },
    // Create blog
    [createBlog.pending]: (state, { payload }) => {
      state.blogs = [];
      state.errors = [];
      state.isDone = false;
    },
    [createBlog.fulfilled]: (state, { payload }) => {
      switch (payload?.status) {
        case 401:
          state.errors = {
            message: "Authorization required. Sign in to your account.!"
          };
          break;
        case 403:
          state.errors = {
            message: "You do not have permission to perform this action !"
          };
          break;
        case 201:
          state.isDone = true;
          break;
        case 422:
          state.errors = payload?.data?.errors;
          break;
        default:
          state.errors = { message: "Something is wrong !" };
      }
    },
    [createBlog.rejected]: (state, { payload }) => {
      console.log("createBlog is rejected ...");
    }
  }
});

export const {} = blogsSlice.actions;
export default blogsSlice.reducer;
