import React, { memo } from "react";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPenAlt, faTrash, faEye } from "@fortawesome/free-solid-svg-icons";

const LineTableBlogs = ({ blog }) => {
  // console.log("LineTableBlogs");
  return (
    <tr>
      <td scope="row">{blog?.id}</td>
      <td>
        <span>{blog?.title}</span>
      </td>
      <td>
        <span>{blog?.body}</span>
      </td>
      <td>
        <NavLink className="" to="/edit-blogs">
          <FontAwesomeIcon className="edit-icon" icon={faPenAlt} />
        </NavLink>
        <span> </span>
        <NavLink className="" to="/edit-blogs">
          <FontAwesomeIcon className="eye-icon" icon={faEye} />
        </NavLink>
        <span> </span>
        <button className="btn-trash">
          <FontAwesomeIcon className="trash-icon" icon={faTrash} />
        </button>
      </td>
    </tr>
  );
};

export default memo(LineTableBlogs);
