import React, { memo, useState } from "react";
import TableBlogs from "./tableData/TableBlogs";
import FilterBlogs from "./tableData/FilterBlogs";

const ManageBlogs = () => {
  const [filter, setFilter] = useState({
    pagination: 1,
    title: "",
    count_per_page: 15
  });
  return (
    <div className="container mt-5">
      <h1>- Manage blogs</h1>
      <FilterBlogs filter={filter} setFilter={setFilter} />
      <TableBlogs filter={filter} setFilter={setFilter} />
    </div>
  );
};

export default memo(ManageBlogs);
