import React, { memo, useState, useRef } from "react";
import { NavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faSearch } from "@fortawesome/free-solid-svg-icons";

const FilterBlogs = ({ filter, setFilter }) => {
  const [filterForm, setFilterForm] = useState({
    title: "",
    count_per_page: 15
  });
  const count_per_page = useRef(filter?.count_per_page);

  const handleChange = (event) => {
    setFilterForm({ ...filterForm, [event.target.name]: event.target.value });
  };

  const handleFilter = (event) => {
    event.preventDefault();
    setFilter({
      ...filter,
      title: filterForm.title,
      pagination: 1,
      count_per_page: count_per_page.current.value
    });
  };

  return (
    <div className="filter-blogs">
      <div className="row">
        <div className="col-sm-6">
          {/* ------------ */}
          <form className="d-flex">
            <select
              name="count_per_page"
              ref={count_per_page}
              onChange={handleFilter}
              value={filter?.count_per_page}
              className="select-pagination"
            >
              <option value="5">5</option>
              <option value="10">10</option>
              <option value="15">15</option>
              <option value="20">20</option>
              <option value="25">25</option>
              <option value="30">30</option>
            </select>

            <input
              onChange={handleChange}
              name="title"
              className="input-search form-control me-2"
              type="search"
              placeholder="Search"
              aria-label="Search"
            />
            <button
              onClick={handleFilter}
              className="btn btn-success"
              type="submit"
            >
              <FontAwesomeIcon icon={faSearch} />
            </button>
          </form>
          {/* ------------ */}
        </div>
        <div className="col-sm-6">
          <NavLink className="btn-new btn btn-primary" to="/add-new-blog">
            <FontAwesomeIcon icon={faPlus} /> Add new
          </NavLink>
        </div>
      </div>
    </div>
  );
};

export default memo(FilterBlogs);
