import React, { useState, useRef, useEffect } from "react";
import "./AddNewBlog.css";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faMinusCircle } from "@fortawesome/free-solid-svg-icons";
import { createBlog } from "../../../store/blogs/blogsActions";
import blogsValidation from "./blogsValidation";

const AddNewBlog = () => {
  const stateData = useSelector((state) => state.blogsSlice);
  const navigate = useNavigate();
  const blogImgRef = useRef();
  const dispatch = useDispatch();
  const [errorsValidate, setErrorsValidate] = useState({});
  const [form, setForm] = useState({
    title: "",
    body: "",
    image: "https://via.placeholder.com/150"
  });

  useEffect(() => {
    if (stateData?.isDone === true) {
      setForm({
        ...form,
        title: "",
        body: "",
        image: "https://via.placeholder.com/150"
      });
      blogImgRef.current.value = "";
    }
  }, [stateData?.isDone]);

  const handleClose = (event) => {
    event.preventDefault();
    navigate("/manage-blogs");
  };

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
  };

  const handleSelectedImg = (event) => {
    // Assuming only image
    var file = event.target.files[0];
    var reader = new FileReader();
    if (file) {
      var url = reader.readAsDataURL(file);
      reader.onloadend = function (e) {
        setForm({ ...form, image: reader.result });
      };
    } else {
      setForm({ ...form, image: "https://via.placeholder.com/150" });
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    let errorsV = {};
    errorsV = blogsValidation(form);
    setErrorsValidate(errorsV);
    if (Object.keys(errorsV).length === 0) {
      const formData = new FormData();
      formData.append("title", form?.title);
      formData.append("body", form?.body);
      if (blogImgRef.current.files[0]) {
        formData.append("image", blogImgRef.current.files[0]);
      }
      dispatch(createBlog(formData));
    }
  };
  console.log(stateData);
  return (
    <section>
      <div className="container mt-5 pt-5">
        <div className="row">
          <div className="col-12 col-sm-10 col-md-6 m-auto">
            <div className="card border-0 shadow">
              <div className="card-body text-center">
                {stateData?.isDone && (
                  <div className="alert alert-warning" role="alert">
                    This blog has been created successfully !
                  </div>
                )}
                {stateData?.errors?.message && (
                  <div className="alert alert-danger" role="alert">
                    {stateData?.errors?.message}
                  </div>
                )}

                <h1> Add new Blog </h1>
                <form
                  encType="multipart/form-data"
                  action=""
                  onSubmit={handleSubmit}
                >
                  <div className="row">
                    <div className="col-md-3">
                      <img
                        src={form?.image}
                        className="img-blog rounded float-left my-4"
                        alt="blogimage"
                      />
                    </div>
                    <div className="col-md-9">
                      <input
                        value={form?.title}
                        name="title"
                        onChange={handleChange}
                        type="text"
                        id="Title"
                        className="form-control my-4 py-2"
                        placeholder="Title"
                      />
                      {errorsValidate?.title && <p>{errorsValidate?.title}</p>}
                      <textarea
                        value={form?.body}
                        name="body"
                        onChange={handleChange}
                        type="text"
                        id="body"
                        className="form-control my-4 py-2"
                        placeholder="Body"
                      />
                      {errorsValidate?.body && <p>{errorsValidate?.body}</p>}
                      <input
                        onChange={handleSelectedImg}
                        ref={blogImgRef}
                        className="form-control"
                        type="file"
                        id="blogImg"
                      />
                    </div>
                  </div>

                  <div className="div-buttons mt-3">
                    <button
                      onClick={handleClose}
                      className="btn btn-primary"
                      type="submit"
                    >
                      Close
                      <span> </span>
                      <FontAwesomeIcon icon={faMinusCircle} />
                    </button>
                    <span> </span>
                    <button className="btn btn-success" type="submit">
                      Submit
                      <span> </span>
                      <FontAwesomeIcon icon={faCheck} />
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default AddNewBlog;
