import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./Login.css";
import { useNavigate } from "react-router-dom";
import { userLogin } from "../../store/auth/userActions";
import loginValidation from "./loginValidation";

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const isLoggedIn = useSelector((state) => state.userSlice.isLoggedIn);
  const errors = useSelector((state) => state.userSlice.errors);
  const [form, setForm] = useState({
    email: "",
    password: ""
  });

  const [errorsValidate, setErrorsValidate] = useState({});

  useEffect(() => {
    if (isLoggedIn === true) {
      navigate("/");
    }
  }, [isLoggedIn]);

  // const handlevalidateError = () => {
  //   validateEmail = "";
  //   validatePassword = "";
  //   if(form.email === ){
  //     validateEmail = 'Email is required §';
  //   }
  // };

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
  };

  const handleSubmitLogin = (event) => {
    event.preventDefault();
    let formErrors = {};
    formErrors = loginValidation(form);
    setErrorsValidate(formErrors);
    if (Object.keys(formErrors).length === 0) {
      dispatch(userLogin(form));
    }
  };

  return (
    <section>
      <div className="container mt-5 pt-5">
        <div className="row">
          <div className="col-12 col-sm-7 col-md-6 m-auto">
            <div className="card border-0 shadow">
              <div className="card-body text-center">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="50"
                  height="50"
                  fill="currentColor"
                  className="mx-left my-3 bi bi-person-circle"
                  viewBox="0 0 16 16"
                >
                  <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
                  <path
                    fillRule="evenodd"
                    d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"
                  />
                </svg>
                <form action="" onSubmit={handleSubmitLogin}>
                  <input
                    value={form.email}
                    name="email"
                    onChange={handleChange}
                    type="text"
                    id="email"
                    className="form-control my-4 py-2"
                    placeholder="Email"
                  />
                  {errorsValidate?.email && (
                    <h6 className="errors">{errorsValidate?.email} </h6>
                  )}
                  {errors?.errors?.email && (
                    <h6 className="errors">{errors?.errors?.email}</h6>
                  )}

                  <input
                    value={form.password}
                    name="password"
                    onChange={handleChange}
                    type="password"
                    id="password"
                    className="form-control my-4 py-2"
                    placeholder="Password"
                  />
                  {errorsValidate?.password && (
                    <h6 className="errors">{errorsValidate?.password} </h6>
                  )}

                  {errors?.errors?.password && (
                    <h6 className="errors">{errors?.errors?.password}</h6>
                  )}
                  {errors?.incorrect_infos && (
                    <h6 className="errors">{errors?.incorrect_infos}</h6>
                  )}
                  <div className="text-center mt-3">
                    <button className="btn btn-primary" type="submit">
                      Login
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Login;
